import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import WorkoutGraph from '../../components/workout/graph';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';
import DogStatus from '../../components/dog/status';

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
		this.setState({ isLoading: true });
        await appState.getFingerprint();
        await userStore.getInfo();
		// await this.loadFeed();
		await this.loadWorkoutSummary();
		await this.loadDogs();
		this.setState({ isLoading: false });
    }

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed({});
    }

	async loadWorkoutSummary() {
		const { userStore } = this.props.stores;
        await userStore.getWorkoutSummary({});
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { isLoading } = this.state;
		const { userStore, dogStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const {
			workouts, workoutSummary,
			teams, tracks,
		} = userStore;

		const { dogs } = dogStore;
		// const dogsInjured = dogStore.findDogsByStatus(2);
		// const dogsPregnant = dogStore.findDogsByStatus(4);
		// const dogsMedication = dogStore.findDogsByStatus(8);
		// const dogsSick = dogStore.findDogsByStatus(9);
		const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		const dogsWithNeeds = dogStore.findDogsWithNeeds();

		const totalTeams = teams.length;
		const totalDogs = dogs.length;
		const totalTracks = tracks.length;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary'>
						<a href='/' class='text-primary stretched-link'><i class='fas fa-chart-line' /> Dashboard</a>
					</div>
					<div class='col text-center'>
						<a href='/feed' class='text-secondary stretched-link'><i class='fas fa-rss' /> Feed</a>
					</div>
					<div class='col text-center'>
						<a href='/todo' class='text-secondary stretched-link'><i class='fas fa-list' /> Todo</a>
					</div>
				</div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						{/* {isLoading && <div class='text-center mt-5 mb-5'>
							<div class="spinner-border text-primary" role="status">
								<span class="sr-only">Loading...</span>
							</div>
						</div>} */}

						{!isLoading && workoutSummary && workoutSummary.length === 0 && (
							<div class='mb-5 py-3 border-top border-bottom'>
								<div class='text-center'>
									<div class='display-4'>
										<i class='fas fa-paw' />
									</div>
									<h5>
										Velkommen til The Musher.
									</h5>
									<p>
										The Musher er en app som er kontinuerlig under utvikling.
										Alle ideer/innspill/kommentarer mottas med stor takk p친 Facebooksiden v친r 游땎
									</p>
									<p>
										Fokus til appen er enkel registrering av hunder, team, treninger og hendelser rundt hundene.
										N친r man har mange hunder s친 er det ofte vanskelig 친 holde styr p친 l칮petid, vaksiner, treninger,
										skader, vetrin칝rbes칮k, chiplister, osv.
									</p>
									<p>
										Alle data lagres sentralt i skyen hos AWS (Amazon Web Services) og det vil derfor v칝re mulig 친 bruke appen fra flere telefoner
										og datamaskiner samtidig. Dette kan v칝re fint dersom det er flere som trener og f칮lger opp hundene.
									</p>
								</div>

								<h5>
									Slik kommer du i gang:
								</h5>
								<ol>
									{totalTeams < 1 && <li><a class='btn btn-info mt-3' href='/teams/'>Legg inn teamene dine.</a></li>}
									{totalDogs < 1 && <li><a class='btn btn-info mt-3' href='/dogs/'>Legg inn hundene dine.</a></li>}
									{totalTracks < 1 && <li><a class='btn btn-info mt-3' href='/tracks/'>Legg inn rundene dine.</a></li>}
									<li><a class='btn btn-info mt-3' href='/workouts/'>Begynn 친 registrere treningene.</a></li>
								</ol>
							</div>
						)}

						<WorkoutGraph stores={this.props.stores} isLoading={isLoading} />
					</div>
				</div>

				<div class='row'>
					{dogsWithStatus && dogsWithStatus.length > 0 && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
						<h5>Hunder som trenger oppf칮lging</h5>
						<div class='row'>
							{dogsWithStatus && dogsWithStatus.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
						</div>
					</div>}					
					{dogsWithNeeds && dogsWithNeeds.length > 0 && <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
						<h5>Hunder med spesielle behov</h5>
						<div class='row'>
							{dogsWithNeeds && dogsWithNeeds.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
						</div>
					</div>}
				</div>

				<div class='row'>
					{workouts && workouts.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>		
							<h5>Siste treningsturer - {workouts.length} stk</h5>
						</div>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>}
				</div>
			</div>
		);
	}
}

export default Home;
